import { Component } from '@angular/core';
import { map } from 'rxjs/operators';

import { ReportsStateService } from '../reports-state.service';

@Component({
    selector: '[report-desks]',
    template: `
        <reports-options></reports-options>
        <div
            class="relative flex-1 h-1/2 w-full overflow-auto print:overflow-visible print:h-auto"
            printable
        >
            <ng-container *ngIf="!(loading | async); else load_state">
                <ng-container *ngIf="total_count | async; else empty_state">
                    <div
                        class="m-4 p-4 rounded bg-base-100 shadow flex justify-center items-center space-x-2"
                    >
                        <div class="flex flex-col items-center flex-1">
                            <h3>Total Bookings</h3>
                            <p>{{ (total_count | async) || 0 }}</p>
                        </div>
                        <div class="flex flex-col items-center flex-1">
                            <h3>Utilisation</h3>
                            <p>{{ (utilisation | async) || 0 }}%</p>
                        </div>
                    </div>
                    <report-desks-charts></report-desks-charts>
                    <report-desks-overall-list
                        [print]="printing"
                    ></report-desks-overall-list>
                    <report-desks-levels-list
                        [print]="printing"
                    ></report-desks-levels-list>
                </ng-container>
            </ng-container>
            <ng-template #load_state>
                <div class="h-full w-full flex flex-col items-center p-8">
                    <mat-spinner [diameter]="32" class="mb-4"></mat-spinner>
                    <p simple>Loading report data...</p>
                </div>
            </ng-template>
            <ng-template #empty_state>
                <div
                    class="h-full w-full flex flex-col items-center p-8 screen-only"
                >
                    <p simple>
                        Select levels and time period to generate a report.
                    </p>
                </div>
            </ng-template>
        </div>
        <button
            icon
            matRipple
            class="absolute top-24 left-4 bg-secondary text-secondary-content border border-base-200 shadow z-20 print:hidden"
            (click)="print()"
            *ngIf="total_count | async"
        >
            <app-icon>print</app-icon>
        </button>
    `,
    styles: [
        `
            :host {
                display: flex;
                flex-direction: column;
                height: 100%;
            }

            h3 {
                font-weight: 500;
            }

            p:not([simple]) {
                font-size: 1.75rem;
            }
        `,
    ],
})
export class ReportDesksComponent {
    public printing = false;
    public readonly total_count = this._state.stats.pipe(
        map((i) => i.count || 0)
    );
    public readonly utilisation = this._state.stats.pipe(
        map((i) => ((i.utilisation || 0) * 100).toFixed(1))
    );
    public readonly loading = this._state.loading;

    constructor(private _state: ReportsStateService) {}

    public print() {
        this.printing = true;
        setTimeout(() => {
            window.print();
            this.printing = false;
        }, 300);
    }

    public ngOnInit() {
        this._state.setOptions({ type: 'desks' });
    }
}
